// Color schemes
$darkerWhite: #F8F8F8;
$titleGreen: #1B75BB;
// Placeholder style mixin
@mixin placeholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content
    }
    &::-moz-placeholder {
        @content
    }
    &:-ms-input-placeholder {
        @content
    }
}

// fade in for on click classes
.fadeIn {
    @include fadeIn();
}
.fadeOut {
    @include fadeOut();
}
