@import '_nav.scss';
@import '_overlay.scss';
.hero {
    @include container;
    height: 100vh;
    &::after {
        content: "";
        position: fixed;
        top: 0;
        z-index: -1;
        background: url('../img/heroBackground.jpg');
        background-size: cover;
        height: 100%;
        width: 100%;
    }
    h1 {
        color: white;
        position: relative;
        text-align: center;
        top: 30%;
        font-weight: 200;
        width: 50%;
        margin: 0 auto;
        @include responsive(mobile) {
            width: 100%;
            font-size: em(30);
        }
        span {
            text-transform: uppercase;
            font-weight: 700;
            display: block;
        }
    }
    .navBar {
        position: absolute;
        bottom: 0%;
        left: 0;
        right: 0;
        .logo {
            display: inline-block;
            @include span(3 of 12);
            margin-left: span(1 of 12);
            img {
                width: 100%;
                // max-width: 100%;
            }
        }
        @include responsive(mobile) {
            position: fixed;
            bottom: 0;
            left: 0;
            right: 0;
            background: black;
            width: 100%;
            z-index: 100000;
            padding: 1px;
        }
        .navButton {
            @include span(1 of 12 last);
            @include responsive(mobile) {
                @include span(2 of 6 last);
            }
            @include responsive(mobile) {
            }
        }
    }
}
